/* src/LandingPage.css */
.landing-page {
    display: flex;
    justify-content: center; /* Center the content horizontally */
    align-items: center; /* Center the content vertically */
    height: 100vh; /* Full viewport height */
    padding: 0 20px;
    text-align: center;
  }
  
  .content-container {
    display: flex;
    flex-direction: column; /* Stack text, image, and button vertically */
    align-items: center; /* Center everything horizontally */
  }
  
  .left-side {
    margin-bottom: 20px; /* Space between the text and the image/button */
  }
  
  .right-side {
    display: flex;
    flex-direction: column; /* Stack image and button vertically */
    align-items: center;
  }
  
  .right-side img {
    width: 200px; /* Make the image a square */
    height: 200px; /* Same height and width for a circle */
    border-radius: 50%; /* Make the image circular */
    object-fit: cover; /* Ensure the image covers the entire area */
    margin-bottom: 20px; /* Space between the image and the button */
  }
  
  .continue-button {
    padding: 12px 30px;
    font-size: 16px;
    background-color: #4CAF50; /* Green color */
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .continue-button:hover {
    background-color: #45a049; /* Darker green on hover */
  }
  